<template>
  {{
    valorCrear == "SI" || valorCrear == "NO" || valorCrear == "N/A"
      ? (capturar(valorCrear).length, (valorCrear = ""))
      : ""
  }}

  <div
    @click="valores.botones = 1"
    v-if="valores.botones === 0"
    class="actividad"
  >
    <div
      class="
        w-6
        h-6
        rounded-full
        flex
        items-center
        justify-center
        border-2
        flex-none
        mr-2
        relative
      "
      :class="{
        'bg-green-200 border-green-500 text-green-500': valores.estado === 1,
        'bg-yellow-200 border-yellow-500 text-yellow-500': valores.estado === 2,
        'bg-naranja1a-200 border-naranja1a-500 text-naranja1a-500':
          valores.estado === 3,
      }"
    >
      <h1 v-if="valores.estado === 1">SI</h1>
      <h1 v-if="valores.estado === 2">NO</h1>
      <h1 class="text-xxs" v-if="valores.estado === 3">N/A</h1>
      <div
        v-if="valores.foto === 1"
        class="
          absolute
          -left-full
          bg-blue-200
          text-blue-500
          flex
          items-center
          justify-center
          w-4
          h-4
          rounded-full
          shadow
        "
      >
        <svg
          xmlns="http://www.w3.org/2000/svg"
          class="w-3 h-3"
          viewBox="0 0 24 24"
          fill="none"
          stroke="currentColor"
          stroke-width="3"
          stroke-linecap="round"
          stroke-linejoin="round"
        >
          <g transform="translate(2 3)">
            <path
              d="M20 16a2 2 0 0 1-2 2H2a2 2 0 0 1-2-2V5c0-1.1.9-2 2-2h3l2-3h6l2 3h3a2 2 0 0 1 2 2v11z"
            />
            <circle cx="10" cy="10" r="4" />
          </g>
        </svg>
      </div>
      <div
        v-if="valores.tienemensaje === true"
        class="
          absolute
          -right-full
          bg-indigo-200
          text-indigo-500
          flex
          items-center
          justify-center
          w-4
          h-4
          rounded-full
          shadow
        "
      >
        <svg
          xmlns="http://www.w3.org/2000/svg"
          class="w-3 h-3"
          viewBox="0 0 24 24"
          fill="none"
          stroke="currentColor"
          stroke-width="3"
          stroke-linecap="round"
          stroke-linejoin="round"
        >
          <path
            d="M21 15a2 2 0 0 1-2 2H7l-4 4V5a2 2 0 0 1 2-2h14a2 2 0 0 1 2 2z"
          ></path>
        </svg>
      </div>
    </div>
    <h1 class="font-bold mr-1">{{ index + 1 }}.</h1>
    <h1 class="">{{ array.actividad }}</h1>
  </div>

  <div v-if="valores.botones === 1" class="actividad">
    <!-- BOTÓN SI -->
    <button @click="capturar('SI')" class="bg-green-200 text-green-500">
      SI
    </button>

    <!-- BOTÓN NO -->
    <button @click="capturar('NO')" class="bg-yellow-200 text-yellow-500">
      NO
    </button>

    <!-- BOTÓN N/A -->
    <button
      @click="capturar('N/A')"
      class="bg-naranja1a-200 text-naranja1a-500"
    >
      N/A
    </button>

    <!-- FOTO -->
    <button class="bg-blue-200 text-blue-500 z-10 relative">
      <input
        type="file"
        class="z-0 text-white opacity-0 absolute w-full"
        @change="
          valores.botones = 0;
          valores.foto = 1;
          subirFoto($event);
        "
      />
      <svg
        xmlns="http://www.w3.org/2000/svg"
        class="w-4 h-4"
        viewBox="0 0 24 24"
        fill="none"
        stroke="currentColor"
        stroke-width="3"
        stroke-linecap="round"
        stroke-linejoin="round"
      >
        <g transform="translate(2 3)">
          <path
            d="M20 16a2 2 0 0 1-2 2H2a2 2 0 0 1-2-2V5c0-1.1.9-2 2-2h3l2-3h6l2 3h3a2 2 0 0 1 2 2v11z"
          />
          <circle cx="10" cy="10" r="4" />
        </g>
      </svg>
    </button>

    <!-- BOTÓN MENSAJE -->
    <button
      @click="
        valores.botones = 3;
        valores.mensaje = 1;
        valores.tienemensaje = true;
      "
      class="bg-indigo-200 text-indigo-500"
    >
      <svg
        xmlns="http://www.w3.org/2000/svg"
        class="w-4 h-4"
        viewBox="0 0 24 24"
        fill="none"
        stroke="currentColor"
        stroke-width="3"
        stroke-linecap="round"
        stroke-linejoin="round"
      >
        <path
          d="M21 15a2 2 0 0 1-2 2H7l-4 4V5a2 2 0 0 1 2-2h14a2 2 0 0 1 2 2z"
        ></path>
      </svg>
    </button>
  </div>

  <div v-if="valores.botones === 3" class="actividad">
    <input
      type="text"
      placeholder="Escriba comentario"
      class="p-2 w-full focus:outline-none"
      v-model="valores.comentario"
    />
    <button
      @click="
        valores.botones = 0;
        valores.tienemensaje = true;
        capturar(valores.valor);
      "
      class="bg-green-200 text-green-500"
    >
      Guardar
    </button>
    <button
      @click="
        valores.botones = 1;
        valores.tienemensaje = false;
      "
      class="bg-red-200 text-red-500"
    >
      Cancelar
    </button>
  </div>
</template>

<script>
import uniqid from "uniqid";

import { ref } from "@vue/reactivity";
import { useStore } from "vuex";
export default {
  props: {
    crear: { type: Object, required: false },
    array: { type: Object, required: false },
    index: { type: Number, value: 0, required: true },
    valorCrear: { type: String },
  },

  setup(props) {
    const store = useStore();
    const Swal = require("sweetalert2");

    const Toast = Swal.mixin({
      toast: true,
      padding: ".3rem",
      background: "#2c3e50",
      iconColor: "#fff",
      position: "top",
      customClass: {
        popup: "colored-toast",
      },
      width: "19rem",
      showConfirmButton: false,
      timer: 2700,
      timerProgressBar: true,
      didOpen: (toast) => {
        toast.addEventListener("mouseenter", Swal.stopTimer);
        toast.addEventListener("mouseleave", Swal.resumeTimer);
      },
    });

    const valores = ref({
      idCaptura: uniqid() + uniqid(),
      idRegistro: props.crear.idRegistro,
      idActividad: props.array.idActividad,
      idEquipo: props.crear.idEquipo,
      estado: 0,
      foto: 0,
      mensaje: 0,
      comentario: "",
      valor: "",
      botones: 0,
      tienemensaje: false,
      urlFoto: "",
    });

    const capturar = async (valor) => {
      if (!valor === "SI" || !valor === "NO" || !valor === "N/A") {
        Toast.fire({
          icon: "info",
          title: "Se requiere seleccionar una Opción!",
        });
        return;
      }

      const data = {
        idDestino: localStorage.getItem("idDestino"),
        idUsuario: localStorage.getItem("usuario"),
        idCaptura: valores.value.idCaptura,
        idRegistro: valores.value.idRegistro,
        idActividad: valores.value.idActividad,
        idEquipo: valores.value.idEquipo,
        action: "crearCaptura",
        valor: valor,
        comentario: valores.value.comentario,
        adjunto: valores.value.urlFoto,
      };

      const response = await store.dispatch("accionesUsuario", data);
      if (response.response) {
        if (response.data.data === "SI") {
          valores.value.botones = 0;
          valores.value.estado = 1;
          valores.value.valor = "SI";
          return;
        }

        if (response.data.data === "NO") {
          valores.value.botones = 0;
          valores.value.estado = 2;
          valores.value.valor = "NO";
          return;
        }

        if (response.data.data === "N/A") {
          valores.value.botones = 0;
          valores.value.estado = 3;
          valores.value.valor = "N/A";
          return;
        }

        return;
      }

      if (!response.response) {
        Toast.fire({
          icon: "warning",
          title: "Se requiere seleccionar un Opción!",
        });
        return;
      }
    };

    const subirFoto = async (event) => {
      const valor = valores.value.valor;
      const urlFoto = uniqid() + uniqid();

      valores.value.urlFoto = urlFoto;

      const file = event.target.files[0];
      const data = new FormData();

      data.append("action", "subirFotoSabanas");
      data.append("idDestino", localStorage.getItem("idDestino"));
      data.append("idUsuario", localStorage.getItem("usuario"));
      data.append("urlFoto", urlFoto);
      data.append("file", file);

      if (
        file.type === "image/jpeg" ||
        file.type === "image/png" ||
        file.type === "image/gif" ||
        file.type === "image/jpg"
      ) {
        const response = await store.dispatch("subirAdjunto", data);
        if (response.resp) {
          valores.value.urlFoto = response.data.urlFoto;
          valores.value.foto = 1;
          capturar(valor);
        }
      } else {
        // Swal.fire("AVISO", "Adjunto No Permitido", "question");
      }
    };

    return { valores, capturar, subirFoto };
  },
};
</script>

<style scoped>
.actividad {
  @apply flex flex-row items-center justify-start text-left w-full py-2 bg-white px-2 text-xs font-semibold border-b;
}

.actividad:hover {
  @apply bg-blue-100;
}

.actividad:last-child {
  @apply border-b-0 rounded-b-xl;
}

.actividad:first-child {
  @apply rounded-t-xl;
}

button {
  @apply p-2 rounded mr-2 font-bold w-1/5 flex items-center justify-center focus:outline-none;
}

button:last-child {
  @apply mr-0;
}

.bg-naranja1a-200 {
  --tw-bg-opacity: 1;
  background-color: rgba(255, 231, 198, var(--tw-bg-opacity));
}

.border-naranja1a-500 {
  --tw-border-opacity: 1;
  border-color: rgba(255, 159, 26, var(--tw-border-opacity));
}

.text-naranja1a-500 {
  --tw-text-opacity: 1;
  color: rgba(255, 159, 26, var(--tw-text-opacity));
}
</style>
