<template>
  <div
    class="w-full h-screen bg-gris-30 flex flex-col items-center justify-start"
  >
    <div class="p-4" v-if="mostrar">
      <h1 class="font-bold uppercase">Registro de sábanas</h1>
    </div>

    <!-- SELECT TIPO DE EQUIPO -->
    <div class="px-4 pb-4 w-full" v-if="mostrar">
      <select
        class="px-2 py-3 rounded-lg focus:outline-none w-full"
        v-model="crear.idHotel"
        @change="consultarTiposActivos()"
      >
        <option value="0">Seleccione Hotel</option>
        <option
          v-for="(item, index) in arrayHoteles"
          :key="index"
          :value="item.idHotel"
        >
          {{ item.hotel }}
        </option>
      </select>
    </div>

    <!-- SELECT TIPO DE EQUIPO -->
    <div class="px-4 pb-4 w-full" v-if="mostrar">
      <select
        class="px-2 py-3 rounded-lg focus:outline-none w-full"
        v-model="crear.tipoActivo"
        @change="consultarSabanas()"
      >
        <option value="0">Seleccione Tipo de Equipo</option>
        <option
          v-for="(item, index) in arrayTiposActivos"
          :key="index"
          :value="item"
        >
          {{ item.tipo }}
        </option>
      </select>
    </div>

    <!-- SELECT DE SABANA -->
    <div class="px-4 pb-4 w-full" v-if="mostrar">
      <select
        class="px-2 py-3 rounded-lg focus:outline-none w-full"
        v-model="crear.idSabana"
        @change="consultarEquipos()"
      >
        <option value="">Seleccione CheckList</option>
        <option
          v-for="(item, index) in arrayListaSabanas"
          :key="index"
          :value="item.idSabana"
        >
          {{ item.sabana }}
        </option>
      </select>
    </div>

    <!-- SELECT DE EQUIPO -->
    <div class="px-4 pb-4 w-full" v-if="mostrar">
      <select
        class="px-2 py-3 rounded-lg focus:outline-none w-full"
        v-model="crear.idEquipo"
      >
        <option value="0">Seleccione Equipo</option>

        <option
          v-for="(item, index) in arrayequipos"
          :key="index"
          :value="item.idEquipo"
        >
          {{ item.equipo }}
        </option>
      </select>
    </div>

    <div class="px-4 pb-4 w-full">
      <button
        v-if="mostrar"
        @click="crearRegistro()"
        class="
          px-2
          py-3
          rounded-lg
          focus:outline-none
          w-full
          bg-bluedemon-500
          text-white
        "
      >
        Crear
      </button>
    </div>

    <div
      v-for="(item, index) in arraySabana"
      :key="index"
      class="px-4 pb-4 w-full flex flex-col items-center justify-start"
    >
      <div class="mb-4">
        <h1 class="font-bold uppercase">{{ item.sabana }}</h1>
      </div>

      <!-- Listado -->
      <div
        class="
          flex flex-col
          items-center
          justify-start
          w-full
          shadow
          rounded-xl
        "
      >
        <div
          class="
            flex flex-col
            items-center
            justify-start
            w-full
            shadow
            rounded-xl
          "
        >
          <Apartadosabanas
            v-for="(item, index) in item.apartados"
            :key="index"
            :array="item"
            :crear="crear"
          />
        </div>
      </div>

      <!-- OPCIONES -->
      <div class="px-4 pb-4 w-full">
        <button
          class="
            px-2
            py-3
            rounded-lg
            focus:outline-none
            w-full
            bg-green-500
            text-white
            mt-4
          "
          @click="confirmarRegistro()"
        >
          Finalizar sábana
        </button>

        <button
          class="
            px-2
            py-3
            rounded-lg
            focus:outline-none
            w-full
            bg-red-500
            text-white
            mt-4
          "
          @click="$router.go($router.currentRoute)"
        >
          Cancelar sábana
        </button>
      </div>
    </div>
  </div>
</template>
<script>
import { ref } from "@vue/reactivity";
import Apartadosabanas from "../components/Apartadosabanas.vue";
import { onMounted } from "@vue/runtime-core";
import { useStore } from "vuex";
import router from "@/router";
import uniqid from "uniqid";

export default {
  components: { Apartadosabanas },
  props: {},
  setup() {
    const store = useStore();
    const Swal = require("sweetalert2");

    const arrayHoteles = ref([]);
    const arrayTiposActivos = ref([]);
    const arraySabana = ref([]);
    const arrayequipos = ref([]);
    const arrayListaSabanas = ref([]);

    const crear = ref({
      idHotel: 0,
      tipoActivo: 0,
      idSabana: "",
      idEquipo: 0,
      idRegistro: "",
      totalActividades: 0,
    });

    const mostrar = ref(true);

    onMounted(async () => {
      const sesion = await store.dispatch("sesion", {
        idDestino: localStorage.getItem("idDestino"),
        idUsuario: localStorage.getItem("usuario"),
        action: "comprobarSesion",
      });

      if (!sesion.response) {
        router.push("login");
        return;
      }

      if (sesion.response) {
        consultarHoteles();
        return;
      }
    });

    const consultarHoteles = async () => {
      // OBTIENE LOS HOTELES DEL DESTINO
      arrayHoteles.value = [];
      arrayTiposActivos.value = [];
      arraySabana.value = [];
      arrayequipos.value = [];
      arrayListaSabanas.value = [];

      crear.value.idHotel = 0;
      crear.value.tipoActivo = 0;
      crear.value.idSabana = "";
      crear.value.idEquipo = 0;
      crear.value.idRegistro = "";
      crear.value.totalActividades = 0;

      const response = await store.dispatch("accionesUsuario", {
        idDestino: localStorage.getItem("idDestino"),
        idUsuario: localStorage.getItem("usuario"),
        action: "consultarHoteles",
      });

      if (response.response) arrayHoteles.value = response.data.data;
    };

    const consultarTiposActivos = async () => {
      // OBTIENE TIPOS DE ACTIVOS
      arrayTiposActivos.value = [];
      arraySabana.value = [];
      arrayequipos.value = [];
      arrayListaSabanas.value = [];

      crear.value.tipoActivo = 0;
      crear.value.idSabana = "";
      crear.value.idEquipo = 0;
      crear.value.idRegistro = "";
      crear.value.totalActividades = 0;

      if (crear.value.idHotel == 0) return;

      const response = await store.dispatch("accionesUsuario", {
        idDestino: localStorage.getItem("idDestino"),
        idUsuario: localStorage.getItem("usuario"),
        action: "consultarTiposActivos",
        idHotel: crear.value.idHotel,
      });

      if (response.response) arrayTiposActivos.value = response.data.data;
    };

    const consultarSabanas = async () => {
      // OBTIENE LOS CHECKLIST
      arraySabana.value = [];
      arrayequipos.value = [];
      arrayListaSabanas.value = [];

      crear.value.idSabana = "";
      crear.value.idEquipo = 0;
      crear.value.idRegistro = "";
      crear.value.totalActividades = 0;

      if (crear.value.idHotel == 0 || crear.value.tipoActivo == 0) return;

      const data = {
        idDestino: localStorage.getItem("idDestino"),
        idUsuario: localStorage.getItem("usuario"),
        idHotel: crear.value.idHotel,
        idTipoActivo: crear.value.tipoActivo.idTipoActivo,
        idRegistroTipoActivo: crear.value.tipoActivo.idRegistroTipoActivo,
        action: "consultaSabanas",
      };

      const response = await store.dispatch("accionesUsuario", data);

      if (response.response) arrayListaSabanas.value = response.data.data;
    };

    const consultarEquipos = async () => {
      // OBTIENE LOS EQUIPOS

      if (
        crear.value.idHotel == 0 ||
        crear.value.tipoActivo == 0 ||
        crear.value.idSabana == ""
      )
        return;

      if (!crear.value.tipoActivo.idTipoActivo) return;

      const data = {
        idDestino: localStorage.getItem("idDestino"),
        idUsuario: localStorage.getItem("usuario"),
        action: "consultarEquipos",
        idSabana: crear.value.idSabana,
        idHotel: crear.value.idHotel,
        idTipoActivo: crear.value.tipoActivo.idTipoActivo,
      };

      const response = await store.dispatch("accionesUsuario", data);
      if (response.response) arrayequipos.value = response.data.data;
    };

    const crearRegistro = async () => {
      const idRegistro = uniqid() + uniqid();
      crear.value.idRegistro = idRegistro;

      if (!crear.value.idSabana.length) return;
      if (crear.value.idEquipo == 0) return;

      const response = await store.dispatch("accionesUsuario", {
        idDestino: localStorage.getItem("idDestino"),
        idUsuario: localStorage.getItem("usuario"),
        idSabana: crear.value.idSabana,
        idEquipo: crear.value.idEquipo,
        idRegistro: idRegistro,
        action: "crearRegistro",
      });

      if (response.response) {
        mostrar.value = false;
        arraySabana.value = response.data.data;
        crear.value.totalActividades = response.data.totalActividades;
      }

      if (!response.response) {
        arraySabana.value = [];
        mostrar.value = true;
      }
    };

    const confirmarRegistro = async () => {
      const response = await store.dispatch("accionesUsuario", {
        idDestino: localStorage.getItem("idDestino"),
        idUsuario: localStorage.getItem("usuario"),
        idSabana: crear.value.idSabana,
        idEquipo: crear.value.idEquipo,
        idRegistro: crear.value.idRegistro,
        totalActividades: crear.value.totalActividades,
        action: "confirmarRegistro",
      });

      if (response.response) {
        if (response.data.completado) {
          mostrar.value = true;
          arraySabana.value = [];

          Swal.fire({
            title: "Actividades Finalizadas!",
            html: `Se han Completado el 100%`,
            icon: "success",
            background: "#2c3e50",
            customClass: {
              popup: "colored-toast",
              backdrop: "swal2-backdrop-show",
              icon: "swal2-icon-show",
            },
            iconColor: "#fff",
          });
          return;
        }

        if (!response.data.completado) {
          Swal.fire({
            title: "Actividades Incompletas!",
            html: `Faltan por Completar el  ${
              crear.value.totalActividades > 0 &&
              response.data.restantes < crear.value.totalActividades
                ? (
                    (100 / crear.value.totalActividades) *
                    response.data.restantes
                  ).toFixed(2)
                : 100
            }% `,
            icon: "info",
            background: "#2c3e50",
            customClass: {
              popup: "colored-toast",
              backdrop: "swal2-backdrop-show",
              icon: "swal2-icon-show",
            },
            iconColor: "#fff",
          });
          return;
        }
      }
      if (!response.response) arraySabana.value = [];
    };

    return {
      mostrar,
      crear,
      consultarTiposActivos,
      consultarEquipos,
      consultarSabanas,
      arrayHoteles,
      arrayTiposActivos,
      arrayListaSabanas,
      arrayequipos,
      arraySabana,
      crearRegistro,
      confirmarRegistro,
    };
  },
};
</script>
<style scoped></style>
